import { Card, Col, Input, Row } from 'antd';
import millify from 'millify';
import { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { instanceCoin } from '../../API/CoinrankingAPI';
import { CatchValueForCoins } from '../../CatchValues';

type PropsRype = {
  simplified: boolean;
};
type SearchCoinType = {
  data: { coins: CoinsType[] };
};
type CoinsType = {
  id: number;
  uuid: string;
  slug: string;
  symbol: string;
  name: string;
  rank: number;
  iconUrl: string;
  price: number;
  marketCap: number;
  change: number;
};

export const CryptocurrenciesPage: React.FC<PropsRype> = ({ simplified }) => {
  const count = simplified ? 10 : 100;

  const [initialCoins, setInitialCoins] = useState<CoinsType[]>([]);
  const [fetching, setFetching] = useState(false);

  useEffect(() => {
    instanceCoin
      .get<SearchCoinType>(`https://coinranking1.p.rapidapi.com/coins?limit=${count}`)
      .then((res) => {
        setInitialCoins(res.data.data.coins);
        setFetching(true);
      })
      .catch((error) => {
        setInitialCoins(CatchValueForCoins);
        setFetching(true);
        console.log(error);
      });
  }, [count]);

  return (
    <>
      {fetching && (
        <Row gutter={[32, 32]} className="crypto-card-container">
          {initialCoins.map((coin) => (
            <Col xs={24} sm={12} lg={6} className="crypto-card" key={coin.id}>
              <NavLink to={`/crypto/${coin.id}`}>
                <Card
                  title={`${coin.rank}.${coin.name}`}
                  extra={<img className="crypto-image" src={coin.iconUrl} />}
                  hoverable>
                  <p>Price:{millify(coin.price)}</p>
                  <p>Market Cup:{millify(coin.marketCap)}</p>
                  <p>Daily Change:{millify(coin.change)}%</p>
                </Card>
              </NavLink>
            </Col>
          ))}
        </Row>
      )}
    </>
  );
};
