import { createSlice } from '@reduxjs/toolkit';
import { getCryptosAPI } from '../API/CoinrankingAPI';

export type DataType = {
  total24hVolume: number;
  totalCoins: number;
  totalExchanges: number;
  totalMarketCap: number;
  totalMarkets: number;
};

const initialState = {
  data: {
    total24hVolume: 0,
    totalCoins: 0,
    totalExchanges: 0,
    totalMarketCap: 0,
    totalMarkets: 0,
  } as DataType,
  fetching: false as boolean,
};

export const globalStats = createSlice({
  name: 'globalStats',
  initialState,
  reducers: {
    setGlobalStats: (state, action) => {
      state.data = action.payload.data;
    },
    isFetching: (state, action) => {
      state.fetching = action.payload;
    },
  },
});

export const { setGlobalStats, isFetching } = globalStats.actions;

export const setGlobalStatsThunkCreator = () => {
  return async (dispatch: any) => {
    dispatch(isFetching(false));
    await getCryptosAPI
      .getCryptos()
      .then((res) => {
        dispatch(setGlobalStats(res));
        dispatch(isFetching(true));
      })
      .catch((error) => {
        console.log(error);

        dispatch(isFetching(true));
      });
  };
};

export default globalStats.reducer;
