import { createSlice } from '@reduxjs/toolkit';
import { getCryptosAPI } from '../API/CoinrankingAPI';

export type DataType = {
  name: string;
  uuid: string;
  slug: string;
  symbol: string;
  circulatingSupply: number;
  numberOfMarkets: number;
  numberOfExchanges: number;
  type: string;
  volume: number;
  marketCap: number;
  price: string;
  description: string;
  totalSupply: number;
  approvedSupply: boolean;
  firstSeen: number;
  listedAt: number;
  change: number;
  rank: number;
  allTimeHigh: {
    price: string;
    timestamp: number;
  };
  links: LinlsType[];
};
type LinlsType = {
  name: string;
  type: string;
  url: string;
};
const initialState = {
  data: {
    name: '',
    uuid: '',
    slug: '',
    symbol: '',
    circulatingSupply: 0,
    numberOfMarkets: 0,
    numberOfExchanges: 0,
    type: '',
    volume: 0,
    marketCap: 0,
    price: '',
    totalSupply: 0,
    description: '',
    approvedSupply: false,
    firstSeen: 0,
    listedAt: 0,
    change: 0,
    rank: 0,
    allTimeHigh: {
      price: '',
      timestamp: 0,
    },
  } as DataType,
  fetching: false as boolean,
};

export const coinInfo = createSlice({
  name: 'coinInfo',
  initialState,
  reducers: {
    setCoinInfo: (state, action) => {
      state.data = action.payload.data.coin;
      debugger;
    },
    isFetching: (state, action) => {
      state.fetching = action.payload;
    },
  },
});

export const { setCoinInfo, isFetching } = coinInfo.actions;

export const setCoinInfoThunkCreator = (coindId: string) => {
  return async (dispatch: any) => {
    debugger;
    dispatch(isFetching(false));
    let response = await getCryptosAPI.getCoin(coindId);
    dispatch(setCoinInfo(response));
    dispatch(isFetching(true));
    debugger;
  };
};

export default coinInfo.reducer;
