import {
  CheckOutlined,
  DollarCircleOutlined,
  ExclamationCircleOutlined,
  FundOutlined,
  MoneyCollectOutlined,
  NumberOutlined,
  StopOutlined,
  ThunderboltOutlined,
  TrophyOutlined,
} from '@ant-design/icons';
import { Col, Row, Select, Typography } from 'antd';
import HTMLReactParser from 'html-react-parser';
import millify from 'millify';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router';
import { setCoinInfoThunkCreator } from '../../redux/coin-page';
import { RootState } from '../../redux/redux-store';
const { Option } = Select;
const { Title, Text } = Typography;
interface ParamTypes {
  coinId: string;
}

export const CryptoDetails = () => {
  const { coinId } = useParams<ParamTypes>();
  const coin = useSelector((state: RootState) => state.coinDetails.data);
  const fetching = useSelector((state: RootState) => state.coinDetails.fetching);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setCoinInfoThunkCreator(coinId));
    debugger;
  }, []);

  const stats = [
    {
      title: 'Price to USD',
      value: `$ ${coin.price && millify(Number(coin.price))}`,
      icon: <DollarCircleOutlined />,
    },
    { title: 'Rank', value: coin.rank, icon: <NumberOutlined /> },
    {
      title: '24h Volume',
      value: `$ ${coin.volume && millify(coin.volume)}`,
      icon: <ThunderboltOutlined />,
    },
    {
      title: 'Market Cap',
      value: `$ ${coin.marketCap && millify(coin.marketCap)}`,
      icon: <DollarCircleOutlined />,
    },
    {
      title: 'All-time-high(daily avg.)',
      value: `$ ${millify(Number(coin.allTimeHigh.price))}`,
      icon: <TrophyOutlined />,
    },
  ];

  const genericStats = [
    { title: 'Number Of Markets', value: coin.numberOfMarkets, icon: <FundOutlined /> },
    { title: 'Number Of Exchanges', value: coin.numberOfExchanges, icon: <MoneyCollectOutlined /> },
    {
      title: 'Aprroved Supply',
      value: coin.approvedSupply ? <CheckOutlined /> : <StopOutlined />,
      icon: <ExclamationCircleOutlined />,
    },
    {
      title: 'Total Supply',
      value: `$ ${millify(coin.totalSupply)}`,
      icon: <ExclamationCircleOutlined />,
    },
    {
      title: 'Circulating Supply',
      value: `$ ${millify(coin.circulatingSupply)}`,
      icon: <ExclamationCircleOutlined />,
    },
  ];

  return (
    <>
      {fetching && (
        <Col className="coin-detail-container">
          <Col className="coin-heading-container">
            <Title level={2} className="coin-name">
              {coin.name}({coin.slug})Price
            </Title>
            <p>
              {coin.name} live price in US dollars. View value statistic, market cap and supply.
            </p>
          </Col>

          <Col className="stats-container">
            <Col className="coin-value-statistics">
              <Col className="coin-value-statistics-heading">
                <Title level={3} className="coin-details-heading">
                  {coin.name} Value statistics
                </Title>
                <p> An overview showing the stats of {coin.name}</p>
              </Col>
              {stats.map(({ icon, title, value }) => (
                <Col className="coin-stats">
                  <Col className="coin-stats-name">
                    <Text>{icon}</Text>
                    <Text>{title}</Text>
                  </Col>
                  <Text className="stats">{value}</Text>
                </Col>
              ))}
            </Col>
          </Col>
          <Col className="other-stats-info">
            <Col className="coin-value-statistics-heading">
              <Title level={3} className="coin-details-heading">
                Other Stats Info
              </Title>
              <p>
                An overview showing the statistics of {coin.name}, such as the base and quote
                currency, the rank, and trading volume.
              </p>
            </Col>
            {genericStats.map(({ icon, title, value }) => (
              <Col className="coin-stats">
                <Col className="coin-stats-name">
                  <Text>{icon}</Text>
                  <Text>{title}</Text>
                </Col>
                <Text className="stats">{value}</Text>
              </Col>
            ))}
          </Col>
          <Col className="coin-desc-link">
            <Row className="coin-desc">
              <Title level={3} className="coin-details-heading">
                What is {coin.name}?
              </Title>
              {HTMLReactParser(coin.description)}
            </Row>
            <Col className="coin-links">
              <Title level={3} className="coin-details-heading">
                {coin.name} Links
              </Title>
              {coin.links.map((link) => (
                <Row className="coin-link" key={link.name}>
                  <Title level={5} className="link-name">
                    {link.type}
                  </Title>
                  <a href={link.url} target="_blank" rel="noreferrer">
                    {link.name}
                  </a>
                </Row>
              ))}
            </Col>
          </Col>
        </Col>
      )}
    </>
  );
};
