import { BrowserRouter, NavLink, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { Navbar } from './components/Navbar/Navbar';
import { CryptocurrenciesPage } from './components/CryptocurrenciesPage/CryptocurrenciesPage';
import { NewsPage } from './components/NewsPage/NewsPage';
import { CryptoDetails } from './components/CryptocurrenciesPage/CryptoDetails';
import './App.css';
import { Layout, Space, Typography } from 'antd';
import { store } from './redux/redux-store';
import { HomePage } from './components/HomePage/HomePage';

export const MainApp = () => {
  return (
    <BrowserRouter>
      <Provider store={store}>
        <div className="app">
          <div className="navbar">
            <Navbar />
          </div>
          <div className="main">
            <Layout>
              <div className="routes">
                <Switch>
                  <Route exact path="/" render={() => <HomePage />} />
                  <Route
                    exact
                    path="/cryptocurrencies"
                    render={() => <CryptocurrenciesPage simplified={false} />}
                  />
                  <Route exact path="/crypto/:coinId" render={() => <CryptoDetails />} />
                  <Route exact path="/news" render={() => <NewsPage simplified={false} />} />
                </Switch>
              </div>
            </Layout>

            <div className="footer">
              <Typography.Title level={5} style={{ color: 'wheat', textAlign: 'center' }}>
                CryptoInfo <br /> All rights reserved
              </Typography.Title>
              <Space>
                <NavLink to="/">Home</NavLink>
                <NavLink to="/cryptocurrencies">Cryptocurrencies</NavLink>

                <NavLink to="/news">News</NavLink>
              </Space>
            </div>
          </div>
        </div>
      </Provider>
    </BrowserRouter>
  );
};
