import axios from 'axios';

export type cryptoDataType = {
  total24hVolume: number;
  totalCoins: number;
  totalExchanges: number;
  totalMarketCap: number;
  totalMarkets: number;
};
export type CryptoType = {
  data: cryptoDataType;
};

export const getCryptosAPI = {
  getCryptos() {
    return instanceCoin.get<GlobalResponseType>(`/stats`).then((res) => res.data);
  },
  getCoin(coinId: string) {
    return instanceCoin.get<GlobalResponseType>(`/coin/${coinId}`).then((res) => res.data);
  },
};

export type GlobalResponseType<D = {}> = {
  data: D;
  resultCode: 'success' | 'error';
};

