import { Avatar, Card, Col, Row } from 'antd';
import Text from 'antd/lib/typography/Text';
import Title from 'antd/lib/typography/Title';
import moment from 'moment';

import React, { useEffect, useState } from 'react';
import { instanceNews } from '../../API/CryptoNewsAPI';
import demoImg from '../../assets/th.jpg';
import { CatchValueForNews } from '../../CatchValues';
type NewsImgType = {
  thumbnail: { contentUrl: string };
};
type ProviderType = {
  image: NewsImgType;
  name: string;
};
type NewsType = {
  name: string;
  url: string;
  provider: ProviderType[];
  image: NewsImgType;
  description: string;
  datePublished: string;
};
type SearNewsType = {
  value: NewsType[];
};
type PropsType = {
  simplified: boolean;
};

export const NewsPage: React.FC<PropsType> = ({ simplified }) => {
  const count = simplified ? 6 : 12;
  const [cryptoNews, setCryptoNews] = useState<NewsType[]>([]);
  const [fetching, setFetching] = useState(false);
  useEffect(() => {
    instanceNews
      .get<SearNewsType>(
        `https://bing-news-search1.p.rapidapi.com/news/search?q=Cryptocurrency&count=${count}`,
      )
      .then((res) => {
        setCryptoNews(res.data.value);
        console.log(res.data.value);
        setFetching(true);
      })
      .catch((error) => {
        //@ts-ignore
        setCryptoNews(CatchValueForNews);
        console.log(error);
        setFetching(true);
      });
  }, [simplified]);
  return (
    <>
      {fetching && (
        <Row gutter={[24, 24]}>
          {cryptoNews.map((news, i) => (
            <Col xs={24} sm={12} lg={8} key={i}>
              <Card hoverable className="news-card">
                <a href={news.url} target="_blank" rel="noreferrer">
                  <div className="news-image-container">
                    <Title className="news-title" level={4}>
                      {news.name}
                    </Title>
                    <img
                      style={{ maxWidth: '200px', maxHeight: '100px' }}
                      src={news.image?.thumbnail?.contentUrl || demoImg}
                      alt="news"
                    />
                  </div>
                  <p>
                    {news.description.length > 100
                      ? `${news.description.substring(0, 100)}...`
                      : news.description}
                  </p>
                  <div className="provider-container">
                    <div>
                      <Avatar src={news.provider[0].image?.thumbnail?.contentUrl || demoImg} />
                      <Text className="provider-name">{news.provider[0].name}</Text>
                    </div>

                    <Text>{moment(news.datePublished).fromNow()}</Text>
                  </div>
                </a>
              </Card>
            </Col>
          ))}
        </Row>
      )}
    </>
  );
};
