import { Col, Row, Statistic, Typography } from 'antd';
import millify from 'millify';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { setGlobalStatsThunkCreator } from '../../redux/home-page';
import { RootState } from '../../redux/redux-store';
import { CryptocurrenciesPage } from '../CryptocurrenciesPage/CryptocurrenciesPage';
import { NewsPage } from '../NewsPage/NewsPage';
export const HomePage = () => {
  const initialGlobalStats = useSelector((state: RootState) => state.homePage.data);
  const fetching = useSelector((state: RootState) => state.homePage.fetching);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setGlobalStatsThunkCreator());
  }, []);
  return (
    <>
      {fetching && (
        <div>
          <Typography.Title level={2} className="heading">
            Global Crypto Stats
          </Typography.Title>
          <Row>
            <Col span={12}>
              <Statistic title="Total Cryptocurrencies" value={initialGlobalStats.totalCoins} />
            </Col>
            <Col span={12}>
              <Statistic
                title="Total Exchanges"
                value={millify(initialGlobalStats.totalExchanges)}
              />
            </Col>
            <Col span={12}>
              <Statistic
                title="Total Market Cap"
                value={millify(initialGlobalStats.totalMarketCap)}
              />
            </Col>
            <Col span={12}>
              <Statistic
                title="Total 24h Volume "
                value={millify(initialGlobalStats.total24hVolume)}
              />
            </Col>
            <Col span={12}>
              <Statistic title="Total Markets" value={millify(initialGlobalStats.totalMarkets)} />
            </Col>
          </Row>
          <div className="home-heading-container">
            <Typography.Title className="home-title">
              Top 10 Cryptocurrencies is the world
            </Typography.Title>
            <Typography.Title className="show-more">
              <NavLink to="/cryptocurrencies">Show more</NavLink>
            </Typography.Title>
          </div>
          <CryptocurrenciesPage simplified={true} />
          <div className="home-heading-container">
            <Typography.Title className="home-title">Latest Crypto News</Typography.Title>
            <Typography.Title className="show-more">
              <NavLink to="/news">Show more</NavLink>
            </Typography.Title>
          </div>
          <NewsPage simplified={true} />
        </div>
      )}
    </>
  );
};
